.Skills {
  &__content-wrapper {
    display: flex;
    padding: 20px;
    gap: 20px;
    &__innerContent {
      flex: 1;
      display: flex;
      flex-direction: column;
      &__Category-text {
        display: inline-block;
        color: var(--yellow-theme-sub-text-color);
        font-size: 22px;
        font-weight: 700;
        line-height: 1rem;
        letter-spacing: 0.5px;
        position: relative;
        text-transform: uppercase;
        padding: 5px 10px;
        border-bottom: 2px solid var(--yellow-theme-main-color);
      }
      &__Category-text::before,
      &__Category-text::after {
        position: absolute;
        content: "";
        width: 2px;
        height: 5px;
        background-color: var(--yellow-theme-main-color);
        bottom: -1px;
      }
      &__Category-text::before {
        left: 0;
      }
      &__Category-text::after {
        right: 0;
      }
      &__progressbar-container{
        width: 100%;
      }
      .progressBar-wrapper{
        padding:50px 0px 0px 0px;

        p{
          font-size: 1.6rem;
          margin: 0 0 10px;
          font-weight: 500;
          text-transform: uppercase;
          color: white;
        }
      }
    }
  }
}
@media only screen and (max-width :768px) {
  .Skills{
    &__content-wrapper{
      flex-direction: column;
    }
  }
  .Skills__content-wrapper__innerContent__progressbar-container{
    margin-bottom: 20px;

  }
}